.project {
  padding-block: 20px;
  background-color: rgba(67, 0, 94, 0.144);
  margin-block: 100px;
}
.project h2 {
  color: #fff;
  text-align: center;
  font-weight: 400;
  padding-bottom: 10px;
  font-family: "Lobster", cursive;
}
.project__github-repo-info {
  display: flex;
  justify-content: center;
}
.project__github-repo-info img {
  margin-inline: 4px;
}
.project__inner-container {
  width: 800px;
  margin-inline: auto;
}

.project__inner-container__yt-review {
  padding-block: 20px;
  display: flex;
  justify-content: center;
}
.project__inner-container__yt-review iframe {
  width: 800px;
  height: 420px;
}

.project__inner-container__project-info {
  display: flex;
  justify-content: space-between;
  position: relative;
  padding-block: 5px;
}
.project__inner-container__project-info .date p {
  color: hsla(0, 0%, 100%, 0.5);
  font-size: 0.9rem;
  margin-bottom: 2px;
}
.project__inner-container__project-info .date p span {
  padding-right: 2px;
  font-size: 1rem;
}
.project__inner-container__project-info .date p:nth-of-type(1) span {
  color: hsla(145, 100%, 50%, 0.6);
}
.project__inner-container__project-info .date p:nth-of-type(2) span {
  color: hsla(342, 100%, 50%, 0.6);
}
.project__inner-container__project-info .languages img {
  margin-inline: 4px;
}

.project__inner-container__description {
  font-size: 1.2rem;
}
.project__inner-container__description h3 {
  padding-block: 10px;
  color: #fff;
}
.project__inner-container__description article {
  color: hsla(0, 0%, 100%, 0.65);
  width: 600px;
  font-size: 1rem;
}

.project__inner-container__github {
  margin-top: 20px;
  font-size: 1.2rem;
  display: flex;
  justify-content: space-around;
}
.project__inner-container__github a {
  color: rgba(255, 255, 255, 0.9254901961);
  padding: 5px 20px;
  background: rgba(170, 0, 162, 0.0941176471);
  border: none;
  position: relative;
  cursor: pointer;
  transition: background-color 200ms ease-in-out;
}
.project__inner-container__github a:nth-of-type(1):hover {
  background-color: hsla(342, 100%, 50%, 0.3);
}
.project__inner-container__github a:nth-of-type(2):hover {
  background-color: hsla(189, 100%, 50%, 0.3);
}
.project__inner-container__github a:nth-of-type(3):hover {
  background-color: hsla(125, 100%, 50%, 0.3);
}